import { isNil } from 'lodash';
import { AdvancedFilterDataTypesConfigsService } from '@bmc-ux/adapt-angular';

export class RxRecordGridAdvancedFilterValue<T = any> {
  constructor(value?: T, namedOptions: string[] = []) {
    this.filterValue = value;

    if (Array.isArray(namedOptions)) {
      this.namedOptions = namedOptions;
    }
  }

  get length(): number {
    return (
      (Array.isArray(this.filterValue) ? this.filterValue.length : isNil(this.filterValue) ? 0 : 1) +
      this.namedOptions.length
    );
  }

  namedOptions: string[] = [];
  filterValue: T;
  title: string;

  static isEmpty(item: RxRecordGridAdvancedFilterValue | any): boolean {
    return item instanceof RxRecordGridAdvancedFilterValue && !item.length;
  }

  static isEmptyWithRange(item: RxRecordGridAdvancedFilterValue | any): boolean {
    return (
      item instanceof RxRecordGridAdvancedFilterValue &&
      !item.namedOptions.length &&
      AdvancedFilterDataTypesConfigsService.isEmptyOnEmptyValues(item.filterValue)
    );
  }
}
